import Cookies from "js-cookies";
import jwt_decode from "jwt-decode";

class JWT {
  getJWT() {
    // Check token in local storage or cookies
    let jwtString = Cookies.getItem("token") || localStorage.getItem("token");
    if (jwtString) {
      try {
        let jwt = JSON.parse(jwtString);
        this.jwt_token = jwt_decode(jwt.jwt);
        let token = `Bearer ${jwt.jwt}`;
        return token;
      } catch (err) {
        return jwtString;
      }
    }
  }

  /**
   * Method used to setHeader
   */
  async setHeader() {
    let token = await this.getJWT();
    let headers = {};
    headers["Authorization"] = token;
    return headers;
  }

  /**
   * Method used to decode JWT
   */
  getJWTDecoded() {
    let jwt = this.getJWT();
    if (!jwt) return;
    return jwt_decode(jwt);
  }
}

export default new JWT();
