<template>
  <div v-if="connection">
    <b-button
      variant="transparent"
      :class="`text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center ${disablePlayButton ? 'voxdesk-btn-disabled' : ''}`"
      v-if="connection"
      @click="
        show_messages = !show_messages;
        is_playing_message = !is_playing_message;
      "
      block
      :disabled="!voice_mail || disablePlayButton"
    >
      <i :class="`fe ${is_playing_message ? 'fe-play-circle' : 'fe-stop-circle text-danger'}`" />
      <small v-if="is_playing_message">Play</small>
      <small class="text-danger" v-else>Stop</small>
    </b-button>

    <div class="audio position-absolute" v-if="show_messages">
      <div v-for="(item, index) in files" :key="index">
        <div class="card-body">
          <p>{{ item.name }}</p>
          <audio controls="controls" :src="item.key" class="height-40"></audio>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import call from "../../../js/dialer/call";
  export default {
    props: {
      connection: Object,
      disablePlayButton: Boolean,
    },
    data() {
      return {
        show_messages: false,
        files: [],
        is_playing_message: true,
      };
    },
    mounted: async function() {
      let region = await call.getRegion();
      this.files = await call.getMedia();
      this.files.forEach((element) => {
        element.name = element.key.split("pbxplus/")[1];
        element.key = `https://ap1-${region}-infinity-user-data.s3.amazonaws.com/${element.key}`;
      });
    },
  };
</script>
<style scoped>
  .audio {
    top: 0;
    right: 0;
  }
</style>
