import axios from "axios";

import JWT from "./jwt";

import Data from "../core/data";
//server url to get user profile
const User_Server = "https://api.${region}.500apps.com/auth/v1/users/domain_user_details?limit=50";
const Server = "https://api.${region}.500apps.com/pbxplus/v1";
const Agent_Capability = "https://pref.${region}.500apps.com/v2/app_pref?where=app_name='${app_name}' and pref_key='twilioDialer'";
const Voicemail_Server = "https://api.${region}.500apps.com/voxdesk/v1/voicemail-drop/${callsid}?audio_url=${audio_url}";

//server url to get media from pbxplus
const Media_Server = "https://api.${region}.500apps.com/files/?app=pbxplus&limit=50";
class DialerData extends Data {
  /**
   * Method used to get profile data
   */
  async getProfile() {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = eval("`" + User_Server + "`");
    let result = await axios.get(url, { headers });
    let decoded = JWT.getJWTDecoded();
    if (!decoded) return "";
    let current_user = result.data.find((id) => id.email == decoded.email);
    return current_user;
  }
  /**
   * Method used to get capability url for visitor
   * @param {String} domain_id - Domain Id
   * @param {String} callflow_id - Flow Id
   */
  async getCapabilityVisitor(domain_id, callflow_id, api_key) {
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = eval("`" + Server + "`");
    let result = await axios.get(`${url}/c2c/${domain_id}/${callflow_id}/${api_key}`);
    return result.data;
  }

  /**
   * Method used to get capability url for agent
   */
  // eslint-disable-next-line
  async getCapabilityAgent(app_name) {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = eval("`" + Agent_Capability + "`");
    let result = await axios.get(`${url}`, { headers });
    return result.data[0];
  }

  /**
   * Method used to send voice email
   * @param {String} callsid - CallSid
   * @param {String} audio_url - audio
   */
  // eslint-disable-next-line
  async sendVoicemail(callsid, audio_url) {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = eval("`" + Voicemail_Server + "`");
    let result = await axios.get(`${url}`, { headers });
    return result;
  }

  /**
   * Method used to get media from pbxplus
   */
  async getMedia() {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = eval("`" + Media_Server + "`");
    let result = await axios.get(`${url}`, { headers });
    return result.data;
  }
}

export default new DialerData();
